@import url("https://fonts.googleapis.com/css?family=News+Cycle");
@keyframes animate-width {
  0% {
    width: 100%; }
  100% {
    width: 0; } }

* {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  background-clip: padding-box;
  margin: 0;
  outline: 0;
  padding: 0; }
  *:before, *:after {
    box-sizing: border-box; }

html, body {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-family: sans-serif;
  overflow-x: hidden;
  position: relative; }

iframe {
  display: block;
  border: 0; }

html {
  font-size: 100%;
  height: 100%; }

body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

fieldset, img {
  border: 0; }

address, caption, cite, code, dfn, var {
  font-style: normal;
  font-weight: 400; }

ol, ul {
  list-style: none; }

caption, th {
  text-align: left; }

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: 400; }

q:before, q:after {
  content: ''; }

abbr, acronym {
  border: 0; }

article, aside, details, figcaption, figure, footer, header, hgroup, main, nav, section, summary {
  display: block; }

audio, canvas, progress, video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden], template {
  display: none; }

a {
  background: transparent;
  color: inherit;
  text-decoration: none; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc; }

pre {
  overflow: auto; }

code, kbd, pre, samp {
  font-family: monospace, monospace;
  font-size: 1em; }

svg:not(:root) {
  overflow: hidden; }

input::-webkit-input-placeholder {
  opacity: 1; }

input:-moz-placeholder {
  opacity: 1; }

input::-moz-placeholder {
  opacity: 1; }

input:-ms-input-placeholder {
  opacity: 1; }

input:placeholder-shown {
  opacity: 1; }

input::placeholder {
  opacity: 1; }

textarea::-webkit-input-placeholder {
  opacity: 1; }

textarea:-moz-placeholder {
  opacity: 1; }

textarea::-moz-placeholder {
  opacity: 1; }

textarea:-ms-input-placeholder {
  opacity: 1; }

textarea:placeholder-shown {
  opacity: 1; }

textarea::placeholder {
  opacity: 1; }

input, textarea, button {
  background: 0;
  border: 0;
  border-radius: 0; }

button, input, optgroup, select, textarea {
  color: inherit;
  font: inherit; }

button {
  overflow: visible; }

button, select {
  text-transform: none; }

button[disabled], html input[disabled] {
  cursor: default; }

button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input, button, textarea {
  line-height: normal; }

input[type='number']::-webkit-inner-spin-button, input[type='number']::-webkit-outer-spin-button {
  height: auto; }

input[type='search'] {
  -webkit-appearance: textfield; }

input[type='search']::-webkit-search-cancel-button, input[type='search']::-webkit-search-decoration {
  -webkit-appearance: none; }

legend {
  border: 0; }

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical; }

optgroup {
  font-weight: 700; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

::-ms-clear, ::-ms-reveal {
  display: none;
  height: 0;
  width: 0; }

img, svg:not(:root) {
  -ms-interpolation-mode: bicubic;
  display: block;
  image-rendering: optimizeQuality;
  max-width: 100%; }

input[type='text'], input[type='button'], input[type='email'], input[type='password'], input[type='submit'], input[type='tel'], input[type='url'], input[type='number'], input[type='reset'], textarea, button {
  -webkit-appearance: none; }

button, input[type='button'], input[type='reset'], input[type='submit'] {
  cursor: pointer;
  user-select: none; }

/* This css is used to design the index.html page that lists other generated assets by Gulp. */
body {
  font-family: News Cycle, sans-serif;
  background-image: linear-gradient(120deg, #f6d365, #fda085);
  background-attachment: fixed;
  background-repeat: no-repeat; }

.container {
  margin: 30px 0; }

.container, h1 {
  text-align: center; }

h1 {
  font-size: 2.5rem;
  margin-bottom: 30px;
  color: #000;
  display: inline-block;
  position: relative;
  padding: 15px 0;
  width: 100%; }

h1:before {
  bottom: 0; }

h1:after, h1:before {
  content: '';
  position: absolute;
  height: 1px;
  width: 105%;
  background: #000;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%); }

h1:after {
  top: 0; }

li {
  margin-bottom: 15px;
  font-size: 1.25rem;
  line-height: 27px; }

li:last-of-type {
  margin-bottom: 0; }

a {
  text-decoration: none;
  color: #fff;
  transition: color .1s ease-in-out; }

a:active, a:hover {
  color: #000; }

.inner {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 480px;
  width: 100%;
  text-align: left; }

ul {
  padding-left: 15px;
  margin-bottom: 20px; }

ul:last-of-type {
  margin-bottom: 0; }

h2 {
  font-size: 24px;
  margin-bottom: 17.5px; }
